import React, { Fragment } from "react";
import { Group } from "@vx/group";

function Node({ node, onClick }) {
  const width = 60;
  const height = 20;
  const width1 = 60;
  const height1 = 20;

  return (
    <Fragment>
      {node.depth === 0 && (
        <circle r={25} fill="url('#lg')" onClick={onClick} />
      )}
      {node.depth === 1 && (
        <rect
          height={height}
          width={width}
          y={-height / 2}
          x={-width / 2}
          fill={"#272b4d"}
          stroke={node.data.children ? "red" : "#26deb0"}
          strokeWidth={2}
          strokeDasharray={!node.data.children ? "2,2" : "0"}
          strokeOpacity={!node.data.children ? 0.6 : 1}
          rx={!node.data.children ? 10 : 0}
          onClick={onClick}
        />
      )}
      {node.depth > 1 && (
        <rect
          height={height1}
          width={width1}
          y={-height1 / 2}
          x={-width1 / 2}
          fill={"#272b4d"}
          stroke={node.data.children ? "yellow" : "#26deb0"}
          strokeWidth={2}
          strokeDasharray={!node.data.children ? "2,2" : "0"}
          strokeOpacity={!node.data.children ? 1 : 1}
          rx={!node.data.children ? 10 : 0}
          onClick={onClick}
        />
      )}
      {node.key == "dat" && <rect width={width1 + 100} />}
      <text
        dy={".33em"}
        fontSize={9}
        fontFamily="Arial"
        textAnchor={"middle"}
        style={{ pointerEvents: "none" }}
        fill={node.depth === 0 ? "red" : node.children ? "white" : "#26deb0"}
      >
        {node.data.name}
      </text>
    </Fragment>
  );
}

export default Node;



